<template>
  <!-- 管理招聘档案页->发布职位招聘信息 -->
  <div class="releaseJobInfo">
    <h1>发布企业职位招聘信息</h1>
    <hr class="hrs">
    <div class="stepsBox">
      <div class="steps qFlex qFlexJustifyAround">
        <div v-for="(item, index) in steps.stepsArry" :key="index" :class="{active: item.active}">
          <div class="qFlex qFlexDirectionColumn">
            <i class="iconfont icon">&#xe63e;</i>
            <div class="text">{{item.text}}</div>
          </div>
          <hr v-if="index !== 1">
        </div>
      </div>
      <div>
        <div class="form1" v-show="formObj.active === 0 ? true : false">
          <el-form
            :label-position="formObj.form1.labelPosition"
            label-width="90px"
            :hide-required-asterisk="formObj.form1.hideRequiredAsterisk"
          >
            <el-form-item label="*活动名称 :">
              <el-input v-model="formObj.form1.name" placeholder="请输入企业名称"></el-input>
            </el-form-item>
            <el-form-item label="*职位名称 :">
              <el-cascader :options="jobCategory" :show-all-levels="false" v-model="formObj.form1.job" placeholder="请填写企业所需职位名称"></el-cascader>
            </el-form-item>
            <el-form-item label="职场分范围 :">
              <el-select v-model="formObj.form1.fraction" placeholder="请选择所需职场分范围">
                <el-option v-for="item in fraction" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="*工作地点 :">
              <el-cascader :options="address" v-model="formObj.form1.address1" :separator="'-'"></el-cascader>
              <el-input v-model="formObj.form1.address2"></el-input>
            </el-form-item>
          </el-form>
          <div class="tip qFlex qFlexDirectionColumn">
            <span>内容提示：</span>
            <span>加*内容职位发布成功后，将无法更改,请仔细填写相关信息。</span>
          </div>
          <div class="next" :class="[formObj.form1.next ? 'active' : '']" @click="next1">下一步</div>
        </div>
        <div class="form2" v-show="formObj.active === 1 ? true : false">
          <el-form label-width="110px">
            <div class="qFlex qFlexJustifyBetween">
              <el-form-item label="*学历要求 :">
                <el-select class="education" v-model="formObj.form2.education" placeholder="学历要求">
                  <el-option
                    v-for="item in educationArry"
                    :key="item.value"
                    :label="item.value"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item class="salary" label="薪资范围 :">
                <div class="qFlex qFlexJustifyBetween">
                  <el-select class="salary1" v-model="formObj.form2.salary.one" placeholder>
                    <el-option
                      v-for="item in salaryArry[0]"
                      :key="item.value"
                      :label="item.value"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                  <span
                    style="padding: 0 20px;"
                    v-show="formObj.form2.salary.one == '面议' ? false : true"
                  >-</span>
                  <el-select
                    class="salary2"
                    v-model="formObj.form2.salary.two"
                    v-show="formObj.form2.salary.one == '面议' ? false : true"
                    placeholder
                  >
                    <el-option
                      v-for="item in salaryArry[1]"
                      :key="item.value"
                      :label="item.value"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </div>
              </el-form-item>
            </div>
            <el-form-item label="*工作经验要求 :">
              <el-select class="experience" v-model="formObj.form2.experience" placeholder="经验要求">
                <el-option
                  v-for="item in experience"
                  :key="item.value"
                  :label="item.value"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="*技术要求 :">
              <el-tag
                :key="item"
                v-for="item in formObj.form2.technology"
                closable
                :disable-transitions="false"
                @close="handleClose(item)"
              >{{item}}</el-tag>
              <el-input
                class="input-new-tag"
                v-if="technology.inputVisible"
                v-model="technology.inputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleInputConfirm"
                @blur="handleInputConfirm"
              ></el-input>
              <el-button v-else class="button-new-tag" size="small" @click="showInput">添加标签</el-button>
            </el-form-item>
            <el-form-item label="职位描述 :">
              <textarea class="position" id cols="30" rows="10" v-model="formObj.form2.position"></textarea>
            </el-form-item>
          </el-form>
          <div class="next" :class="[formObj.form2.next ? 'active' : '']" @click="next2">下一步</div>
        </div>
        <div class="form3" v-show="formObj.active === 2 ? true : false">
          <div class="qFlex qFlexDirectionColumn">
            <div class="title">
              <img src="../../assets/images/staffCommendationInfo/Successfulpostrelease.png">
              <p class="p1">职位发布成功</p>
              <p class="p2">该职位可免费在线15天，请及时延长招聘时长.</p>
            </div>
            <div class="backBtn qFlex qFlexJustifyBetween">
              <div>添加招聘信息</div>
              <div>返回发布职位首页</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import address from "../../assets/js/base/address.js";
import jobCategory from "../../assets/js/base/jobCategory.js";
import releaseJobInfo from "../../assets/js/manageRecruitmentFiles/releaseJobInfo.js";
export default {
  name: "releaseJobInfo",
  data() {
    return {
      address: address,
      jobCategory: jobCategory,
      steps: releaseJobInfo.steps,
      fraction: releaseJobInfo.fraction,
      educationArry: releaseJobInfo.educationArry,
      salaryArry: releaseJobInfo.salary,
      experience: releaseJobInfo.experience,
      technology: releaseJobInfo.technology,
      formObj: releaseJobInfo.formObj
    };
  },
  methods: {
    /**
     * 第一步确认校验
     */
    checkedNext1: function() {
      if (
        this.formObj.form1.name !== "" &&
        this.formObj.form1.job !== "" &&
        this.formObj.form1.fraction !== "" &&
        this.formObj.form1.address1 !== "" &&
        this.formObj.form1.address2 !== ""
      ) {
        this.formObj.form1.next = 1;
      } else {
        this.formObj.form1.next = 0;
      }
    },
    /**
     * 第一步确认按钮
     */
    next1: function() {
      if (this.formObj.form1.next) {
        this.steps.active = 1;
        this.steps.stepsArry[this.steps.active].active = 1;
        this.formObj.active = 1;
      }
    },
    /**
     * 第二步确认校验
     */
    checkedNext2: function() {
      if (
        this.formObj.form2.education !== "" &&
        ((this.formObj.form2.salary.one !== "" &&
          this.formObj.form2.salary.two !== "") ||
          this.formObj.form2.salary.one === "面议") &&
        this.formObj.form2.experience !== "" &&
        this.formObj.form2.technology.length !== 0 &&
        this.formObj.form2.position !== ""
      ) {
        this.formObj.form2.next = 1;
      } else {
        this.formObj.form2.next = 0;
      }
    },
    /**
     * 第二步确认按钮
     */
    next2: function() {
      if (this.formObj.form2.next) {
        this.steps.active = 2;
        this.steps.stepsArry[this.steps.active].active = 1;
        this.formObj.active = 2;
      }
    },
    /**
     * 第三步确认按钮
     */
    next3: function() {},
    /**
     * 标签
     */

    handleClose(tag) {
      this.formObj.form2.technology.splice(
        this.formObj.form2.technology.indexOf(tag),
        1
      );
    },

    showInput() {
      this.technology.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    handleInputConfirm() {
      let inputValue = this.technology.inputValue;
      if (inputValue) {
        this.formObj.form2.technology.push(inputValue);
      }
      this.technology.inputVisible = false;
      this.technology.inputValue = "";
    }
  },
  watch: {
    /**
     * 监控第一步输入情况
     */
    "formObj.form1.name": function() {
      this.checkedNext1();
    },
    "formObj.form1.job": function() {
      this.checkedNext1();
    },
    "formObj.form1.fraction": function() {
      this.checkedNext1();
    },
    "formObj.form1.address1": function() {
      this.checkedNext1();
    },
    "formObj.form1.address2": function() {
      this.checkedNext1();
    },
    /**
     * 监控第二步输入情况
     */
    "formObj.form2.education": function() {
      this.checkedNext2();
    },
    "formObj.form2.salary.one": function() {
      this.checkedNext2();
    },
    "formObj.form2.salary.two": function() {
      this.checkedNext2();
    },
    "formObj.form2.experience": function() {
      this.checkedNext2();
    },
    "formObj.form2.technology": function() {
      this.checkedNext2();
    },
    "formObj.form2.position": function() {
      this.checkedNext2();
    },
    /**
     * 监控薪资范围最低值
     */
    "formObj.form2.salary.one": function(oldData, newData) {
      for (var i = 0; i < this.salaryArry[0].length; i++) {
        if (this.formObj.form2.salary.one === this.salaryArry[0][i].value) {
          this.salaryArry.minId = i;
          break;
        }
      }
      if (this.salaryArry.minId !== "" && this.salaryArry.minId !== 0) {
        this.salaryArry[1] = [];
        var len = 251 - this.salaryArry.minId;
        for (var j = 0; j < len; j++) {
          this.salaryArry[1].push({
            id: j,
            value: j + 1 + this.salaryArry.minId + "K"
          });
        }
        this.formObj.form2.salary.two = this.salaryArry[1][0];
      } else {
        this.formObj.form2.salary.two = "";
      }
    }
  }
};
</script>

<style scoped>
@import "../../assets/css/manageRecruitmentFiles/releaseJobInfo.css";
</style>



