<template>
  <!-- 管理员工档案 -->
  <div class="manageStaffFiles">
    <div class="topBtn qFlex qFlexJustifyBetween">
      <div class="topLeftBtn qFlex qFlexJustifyStart">
        <div
          class="LeftBtnItem"
          @click="leftBtnSwitch(index)"
          :class="{active: item.active}"
          v-for="(item, index) in leftBtn"
          :key="index"
          tag="a"
          to
        >
          <span>
            {{item.text}}
            <font v-if="item.num">({{item.num}})</font>
          </span>
        </div>
      </div>
      <div class="topRightBtn qFlex qFlexJustifyStart">
        <div class="search">
          <input type="text" placeholder="搜索人员">
          <i class="el-icon-search"></i>
        </div>
        <font class="shuxian"></font>
        <div class="addstaff">
          <router-link tag="a" to>
            <img src="../../assets/images/manageStaffFiles/AddstaffIcon.png">
            <span>添加新员工</span>
          </router-link>
        </div>
      </div>
    </div>
    <table class="tables" cellspacing="0" cellpadding="0">
      <thead class="thead qFlex qFlexJustifyStart">
        <tr>
          <td class="check">
            <img @click="allElection" :src="allElectionBtn[allElectionBtn.a]">
          </td>
          <td class="nameBtn">
            <span>姓名</span>
						<font class="shuxian"></font>
          </td>
          <td class="statusBtn">
            <span>状态</span>
						<font class="shuxian"></font>
          </td>
          <td class="departmentBtn">
            <span>所属部门</span>
						<font class="shuxian"></font>
          </td>
          <td class="positionBtn">
            <span>职位</span>
						<font class="shuxian"></font>
          </td>
          <td class="entryTimeBtn">
            <span>入职时间</span>
            <i :class="[timeBtn[0]]"></i>
						<font class="shuxian"></font>
          </td>
          <td class="operationBtn">
            <span>操作</span>
          </td>
        </tr>
      </thead>
      <tbody class="tbody">
        <tr
          class="tr qFlex qFlexJustifyStart"
          :class="{active: item.active}"
          @mousemove="tabMouseleave(index)"
          @mouseout="tabMouseout"
          v-for="(item, index) in tbody"
          :key="index"
        >
          <td class="check">
            <img @click="singleElection(index)" :src="item.check[item.checked]">
          </td>
          <td class="name">
            <span>{{item.name}}</span>
          </td>
          <td class="status qFlex qFlexJustifyCenter qFlexAlignCenter">
            <img :src="item.statusBtn[item.status]">
          </td>
          <td class="department">
            <span>{{item.department}}</span>
          </td>
          <td class="position">
            <span>{{item.position}}</span>
          </td>
          <td class="entryTime">
            <span>{{item.entryTime}}</span>
          </td>
          <td class="operation">
            <span>编辑</span>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="qFlex qFlexJustifyCenter">
      <div class="deleteBtn" @click="deleteItem">
        <img :src="deleteBtn.icon[deleteBtn.a]">
        <span :style="deleteBtn.color[deleteBtn.a]">删除</span>
      </div>
      <div class="pagination qFlex qFlexJustifyStart">
        <div class="firstPage">首页</div>
        <div class="prevPage">上一页</div>
        <div class="pageList">
          <div>1</div>
        </div>
        <div class="nextPage">下一页</div>
        <div class="lastPage">尾页</div>
        <div class="pages">
          共
          <span>2</span>页
        </div>
        <div class="sizes">
          共
          <span>2</span>条记录
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jQuery";
export default {
  name: "manageStaffFiles",
  data() {
    return {
      timeBtn: ["el-icon-caret-bottom", "el-icon-caret-top"],
      allElectionBtn: {
        a: 0,
        0: require("../../assets/images/manageStaffFiles/Checkboxunchecked.png"),
        1: require("../../assets/images/manageStaffFiles/Checkbox.png")
      },
      deleteBtn: {
        a: 0,
        icon: [
          require("../../assets/images/manageStaffFiles/Deleteinactivated.png"),
          require("../../assets/images/manageStaffFiles/Deleteactivation.png")
        ],
        color: ["color: rgba(238, 238, 238, 1);", "color: rgba(253,37,37,1);"]
      },
      leftBtn: [
        { text: "在职员工", num: "1000人", active: 1 },
        { text: "离职员工", active: 0 }
      ],
      tbody: [
        {
          active: 1,
          checked: 0,
          status: 0,
          statusBtn: [
            require("../../assets/images/manageStaffFiles/Tobeconfirmed.png"),
            require("../../assets/images/manageStaffFiles/Confirmed.png")
          ],
          check: [
            require("../../assets/images/manageStaffFiles/Checkboxunchecked.png"),
            require("../../assets/images/manageStaffFiles/Checkbox.png")
          ],
          name: "王启明",
          department: "产品运营与公关",
          position: "销售经理",
          entryTime: "2018-11-23"
        },
        {
          active: 0,
          checked: 0,
          status: 0,
          statusBtn: [
            require("../../assets/images/manageStaffFiles/Tobeconfirmed.png"),
            require("../../assets/images/manageStaffFiles/Confirmed.png")
          ],
          check: [
            require("../../assets/images/manageStaffFiles/Checkboxunchecked.png"),
            require("../../assets/images/manageStaffFiles/Checkbox.png")
          ],
          name: "王启明",
          department: "产品运营与公关",
          position: "销售经理",
          entryTime: "2018-11-23"
        }
      ]
    };
  },
  methods: {
    leftBtnSwitch: function(index) {
      console.log(index);
      for (var i = 0; i < this.leftBtn.length; i++) {
        this.leftBtn[i].active = 0;
      }
      for (var i = 0; i < this.leftBtn.length; i++) {
        if (i === index) {
          if (this.leftBtn[i].active) {
            this.leftBtn[i].active = 0;
          } else {
            this.leftBtn[i].active = 1;
          }
        }
      }
    },
    /**
     * 鼠标滑过
     */
    tabMouseleave: function(index) {
      for (var i = 0; i < this.tbody.length; i++) {
        if (index === i) {
          this.tbody[i].active = 1;
        } else {
          this.tbody[i].active = 0;
        }
      }
    },
    tabMouseout: function() {
      for (var i = 0; i < this.tbody.length; i++) {
        this.tbody[i].active = 0;
      }
    },
    /**
     * 单选
     */
    singleElection: function(index) {
      var all = 0;
      for (var i = 0; i < this.tbody.length; i++) {
        if (index === i) {
          if (this.tbody[i].checked) {
            this.tbody[i].checked = 0;
          } else {
            this.tbody[i].checked = 1;
          }
        }
        if (this.tbody[i].checked) {
          all++;
        }
      }
      /**
       * 勾选全选按钮
       */
      if (all === this.tbody.length) {
        this.allElectionBtn.a = 1;
      } else {
        this.allElectionBtn.a = 0;
      }
      /**
       * 显示删除
       */
      if (all > 0) {
        this.deleteBtn.a = 1;
      } else {
        this.deleteBtn.a = 0;
      }
    },
    /**
     * 全选
     */
    allElection: function() {
      if (this.allElectionBtn.a) {
        this.allElectionBtn.a = 0;
      } else {
        this.allElectionBtn.a = 1;
      }
      for (var i = 0; i < this.tbody.length; i++) {
        this.tbody[i].checked = this.allElectionBtn.a;
      }
      this.allElectionBtn.a = this.allElectionBtn.a;
      this.deleteBtn.a = this.allElectionBtn.a;
    },
    /**
     * 删除
     */
    deleteItem: function() {
      for (var i = 0; i < this.tbody.length; i++) {
        if (this.tbody[i].checked) {
          this.tbody.splice(1, i);
        }
      }
    }
  }
};
</script>

<style scoped>
@import "../../assets/css/index/manageStaffFiles.css";
</style>



