<template>
  <!-- 管理招聘档案页->授权待通过 -->
  <div class="authorizationing">
    <div class="topBtn qFlex qFlexJustifyBetween">
      <div class="topLeftBtn qFlex qFlexJustifyStart">
        <router-link
          class="LeftBtnItem"
          :class="{active: item.active}"
          v-for="item in activeLeftBtn"
          :key="item.id"
          tag="a"
          to
        >
          <span>{{item.text}}</span>
        </router-link>
      </div>
    </div>
    <table cellspacing="0" cellpadding="0">
      <thead>
        <tr>
          <td class="infoBtn">
            <span>基本信息</span>
            <font class="shuxian"></font>
          </td>
          <td class="fractionBtn">
            <span>职场分</span>
            <font class="shuxian"></font>
          </td>
          <td class="typeBtn">
            <span>类型</span>
            <font class="shuxian"></font>
          </td>
          <td class="expectPositionBtn">
            <span>期望工作职位</span>
            <font class="shuxian"></font>
          </td>
          <td class="operationBtn">
            <span>操作</span>
          </td>
        </tr>
      </thead>
      <tbody>
        <tr
          :class="{active: item.active}"
          v-for="(item, index) in tbody"
          :key="index"
          @mousemove="tabMouseleave(index)"
          @mouseout="tabMouseout"
        >
          <td class="info qFlex qFlexJustifyStart qFlexAlignCenter">
            <div class="headimg">
              <img :src="item.headimg" alt>
              <img class="sex" :src="item.sex.icon[item.sex.active]" alt>
            </div>
            <div class="qFlex qFlexDirectionColumn">
              <div class="name">
                <span>{{item.name}}</span>
              </div>
              <div class="itemInfo">
                <span>{{item.info.companyName}}</span>/
                <span>{{item.info.job}}</span>
              </div>
            </div>
          </td>
          <td class="fraction">
            <span>{{item.fraction}}分</span>
          </td>
          <td class="type">
            <span>{{item.type}}</span>
          </td>
          <td class="expectPosition">
            <span>{{item.expectPosition}}</span>
          </td>
          <td class="operation">
            <div>{{item.operation[item.active]}}</div>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="qFlex qFlexJustifyCenter">
      <div class="pagination qFlex qFlexJustifyStart">
        <div class="firstPage">首页</div>
        <div class="prevPage">上一页</div>
        <div class="pageList">
          <div>1</div>
        </div>
        <div class="nextPage">下一页</div>
        <div class="lastPage">尾页</div>
        <div class="pages">
          共
          <span>2</span>页
        </div>
        <div class="sizes">
          共
          <span>2</span>条记录
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "authorizationing",
  data() {
    return {
      activeLeftBtn: [
        { text: "授权通过", active: 0 },
        { text: "授权待通过", active: 1 },
        { text: "拒绝授权", active: 0 }
      ],
      tbody: [
        {
          active: 0,
          headimg: require("../../assets/images/index/userImage.png"),
          sex: {
            active: 0,
            icon: [
              require("../../assets/images/index/Female.png"),
              require("../../assets/images/index/Male.png")
            ]
          },
          name: "王明阳",
          info: {
            companyName: "北京民富网络科技有限公司",
            job: "产品经理"
          },
          fraction: 778,
          type: "优秀职场人",
          expectPosition: "市场运营",
          operation: ["立即沟通", "继续沟通"]
        },
        {
          active: 0,
          headimg: require("../../assets/images/index/userImage.png"),
          sex: {
            active: 0,
            icon: [
              require("../../assets/images/index/Female.png"),
              require("../../assets/images/index/Male.png")
            ]
          },
          name: "王明阳",
          info: {
            companyName: "北京民富网络科技有限公司",
            job: "产品经理"
          },
          fraction: 778,
          type: "优秀职场人",
          expectPosition: "市场运营",
          operation: ["立即沟通", "继续沟通"]
        },
        {
          active: 0,
          headimg: require("../../assets/images/index/userImage.png"),
          sex: {
            active: 1,
            icon: [
              require("../../assets/images/index/Female.png"),
              require("../../assets/images/index/Male.png")
            ]
          },
          name: "王明阳",
          info: {
            companyName: "北京民富网络科技有限公司",
            job: "产品经理"
          },
          fraction: 778,
          type: "优秀职场人",
          expectPosition: "市场运营",
          operation: ["立即沟通", "继续沟通"]
        }
      ]
    };
  },
  methods: {
    /**
     * 鼠标滑过
     */
    tabMouseleave: function(index) {
      for (var i = 0; i < this.tbody.length; i++) {
        if (index === i) {
          this.tbody[i].active = 1;
        } else {
          this.tbody[i].active = 0;
        }
      }
    },
    tabMouseout: function() {
      for (var i = 0; i < this.tbody.length; i++) {
        this.tbody[i].active = 0;
      }
    }
  }
};
</script>

<style scoped>
@import "../../assets/css/manageRecruitmentFiles/authorizationing.css";
</style>



