<template>
  <!-- 管理招聘档案页->看过我 -->
  <div class="readMe">
    <div class="topBtn qFlex qFlexJustifyBetween">
      <div class="topLeftBtn qFlex qFlexJustifyStart">
        <div
          class="LeftBtnItem"
          @click="leftBtnSwitch(index)"
          :class="{active: item.active}"
          v-for="(item, index) in leftBtn"
          :key="index"
          tag="a"
          to
        >
          <span>{{item.text}}</span>
        </div>
      </div>
      <div class="topRightBtn qFlex qFlexJustifyStart">
        <div class="search">
          <input type="text" placeholder="搜索人员">
          <i class="el-icon-search"></i>
        </div>
        <font class="shuxian"></font>
        <div class="refresh">
          <router-link tag="a" to>
            <img src="../../assets/images/manageStaffFiles/AddstaffIcon.png">
            <span>刷新</span>
          </router-link>
        </div>
      </div>
    </div>
    <div class="tables">
      <table cellspacing="0" cellpadding="0">
        <thead>
          <tr>
            <td class="infoBtn">
              <span>基本信息</span>
              <font class="shuxian"></font>
            </td>
            <td class="advantageBtn">
              <span>个人优势</span>
              <font class="shuxian"></font>
            </td>
            <td class="positionBtn">
              <span>查看过的职位</span>
            </td>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in tbody"
            :key="index"
            :class="{active: item.active}"
            @mousemove="tabMouseleave(index)"
            @mouseout="tabMouseout"
          >
            <td class="info">
              <div class="qFlex qFlexJustifyStart">
                <div class="headimg">
                  <img :src="item.headimg" alt="头像">
                  <img class="sex" :src="item.sex.icon[item.sex.active]" :alt="item.sex.icon.alt">
                </div>
                <div class="qFlex qFlexDirectionColumn">
                  <div class="name">
                    <span>{{item.name}}</span>
                  </div>
                  <div class="jonInfo">
                    <span>{{item.jobInfo.companyName}}</span>
                    <font>/</font>
                    <span>{{item.jobInfo.job}}</span>
                  </div>
                  <div class="tags qFlex qFlexJustifyStart">
                    <div>
                      <span>{{item.jobInfo.tags.address}}</span>
                    </div>
                    <font class="shuxian"></font>
                    <div>
                      <span>{{item.jobInfo.tags.education}}</span>
                    </div>
                    <font class="shuxian"></font>
                    <div>
                      <span>{{item.jobInfo.tags.time}}工作经验</span>
                    </div>
                    <font class="shuxian"></font>
                    <div>
                      <span>{{item.jobInfo.tags.age}}岁</span>
                    </div>
                  </div>
                </div>
              </div>
            </td>
            <td class="advantage">
              <div class="showByStep2">{{item.advantage}}</div>
            </td>
            <td class="position">
              <div>{{item.position.text[item.position.active]}}</div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="qFlex qFlexJustifyCenter">
      <div class="pagination qFlex qFlexJustifyStart">
        <div class="firstPage">首页</div>
        <div class="prevPage">上一页</div>
        <div class="pageList">
          <div>1</div>
        </div>
        <div class="nextPage">下一页</div>
        <div class="lastPage">尾页</div>
        <div class="pages">
          共
          <span>2</span>页
        </div>
        <div class="sizes">
          共
          <span>2</span>条记录
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import readMe from "../../assets/js/manageRecruitmentFiles/readMe.js";
export default {
  name: "readMe",
  data() {
    return {
      leftBtn: readMe.leftBtn,
      tbody: readMe.tbody
    };
  },
  methods: {
    leftBtnSwitch: function(index) {},
    /**
     * 鼠标滑过
     */
    tabMouseleave: function(index) {
      for (var i = 0; i < this.tbody.length; i++) {
        if (index === i) {
          this.tbody[i].active = 1;
          this.tbody[i].position.active = 1;
        } else {
          this.tbody[i].active = 0;
          this.tbody[i].position.active = 0;
        }
      }
    },
    tabMouseout: function() {
      for (var i = 0; i < this.tbody.length; i++) {
        this.tbody[i].active = 0;
        this.tbody[i].position.active = 0;
      }
    }
  }
};
</script>

<style scoped>
@import "../../assets/css/manageRecruitmentFiles/readMe.css";
</style>



