<template>
  <!-- 管理招聘档案页->热招职位 -->
  <div class="hotTrick">
    <div class="topBtn qFlex qFlexJustifyBetween">
      <div class="topLeftBtn qFlex qFlexJustifyStart">
        <div
          class="LeftBtnItem"
          @click="leftBtnSwitch(index)"
          :class="{active: item.active}"
          v-for="(item, index) in leftBtn"
          :key="index"
          tag="a"
          to
        >
          <span>
            {{item.text}}
            <font v-if="item.num">({{item.num}})</font>
          </span>
        </div>
      </div>
      <div class="topRightBtn qFlex qFlexJustifyStart">
        <router-link
          class="RightBtnItem"
          :class="{active: item.active}"
          v-for="item in rightBtn"
          :key="item.id"
          tag="a"
          to
        >
          <span>{{item.text}}</span>
          <i :class='[item.active ? "el-icon-caret-top" : "el-icon-caret-bottom"]'></i>
        </router-link>
      </div>
    </div>
		<table cellspacing="0" cellpadding="0">
			<tbody>
          <tr
            v-for="(item, index) in tbody"
            :key="index"
            :class="{active: item.active}"
            @mousemove="tabMouseleave(index)"
            @mouseout="tabMouseout"
          >
					<td>
						<div class="qFlex qFlexJustifyStart">
							<div class="headimg">
								<img :src="item.headimg" alt="头像">
								<img class="sex" :src="item.sex.icon[item.sex.active]" :alt="item.sex.icon.alt">
							</div>
							<div class="qFlex qFlexDirectionColumn">
								<div class="name qFlex qFlexJustifyStart">
									<span>孙定剑</span>
									<font class="shuxian"></font>
									<span>技术总监</span>
								</div>
								<div class="tags qFlex qFlexJustifyStart">
									<div class="qFlex qFlexJustifyStart">
										<span>3年</span>
										<font class="shuxian"></font>
									</div>
									<div class="qFlex qFlexJustifyStart">
										<span>本科</span>
										<font class="shuxian"></font>
									</div>
									<div class="qFlex qFlexJustifyStart">
										<span>企业CEO</span>
										<font class="shuxian"></font>
									</div>
									<div class="qFlex qFlexJustifyStart">
										<span>领导能力强</span>
										<font class="shuxian"></font>
									</div>
									<div class="qFlex qFlexJustifyStart">
										<span>技术控</span>
									</div>
								</div>
							</div>
						</div>
					</td>
					<td class="job">
						<span>大数据挖掘工程师</span>
					</td>
					<td class="salary">
						<span>12K-29K</span>
					</td>
					<td class="Communicate">
						<div v-show="item.active">立即沟通</div>
					</td>
				</tr>
			</tbody>
		</table>
    <div class="qFlex qFlexJustifyCenter">
      <div class="pagination qFlex qFlexJustifyStart">
        <div class="firstPage">首页</div>
        <div class="prevPage">上一页</div>
        <div class="pageList">
          <div>1</div>
        </div>
        <div class="nextPage">下一页</div>
        <div class="lastPage">尾页</div>
        <div class="pages">
          共
          <span>2</span>页
        </div>
        <div class="sizes">
          共
          <span>2</span>条记录
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import hotTrick from "../../assets/js/manageRecruitmentFiles/hotTrick.js";
export default {
  name: "hotTrick",
  data() {
    return {
			leftBtn: hotTrick.leftBtn,
			rightBtn: hotTrick.rightBtn,
			tbody: hotTrick.tbody
    };
	},
	methods:{
		
    /**
     * 鼠标滑过
     */
    tabMouseleave: function(index) {
      for (var i = 0; i < this.tbody.length; i++) {
        if (index === i) {
          this.tbody[i].active = 1;
        } else {
          this.tbody[i].active = 0;
        }
      }
    },
    tabMouseout: function() {
      for (var i = 0; i < this.tbody.length; i++) {
        this.tbody[i].active = 0;
      }
    }
	}
};
</script>

<style scoped>
@import '../../assets/css/manageRecruitmentFiles/hotTrick.css';
</style>



