                <span @click="changeyzm">看不清？换下一张</span>
              </el-form-item>
              <el-form-item>
                <el-button class="loginSubmit" type="primary" @click="login">登录</el-button>
              </el-form-item>
            </el-form>
            <div class="otherTitle">
              <span>其他方式登录</span>
              <hr>
            </div>
            <div class="otherLogin">
              <router-link tag="a" to>
                <img class="wechart" src="../assets/images/login/Wechatwasnotselected.png">
              </router-link>
              <router-link class="message" tag="a" to>
                <img src="../assets/images/login/Textmessagenotselected.png">
              </router-link>
            </div>
            <div class="registerAndModifypaswd">
              <router-link class="modifyPaswd" tag="a" to>忘记密码?</router-link>
              <router-link class="register" tag="a" to>下载App，去注册 &gt;</router-link>
            </div>
          </div>
          <div v-show="!changeBtn" class="sao-login">
            <h2>扫码登录</h2>
            <hr class="width268">
            <div class="qrcode"></div>
            <p class="p1">
              请使用职场档案APP扫二维码
              <img src="../assets/images/login/help.png">
            </p>
            <p class="p2">如多次扫描失败</p>
            <p class="p3">请鼠标移入二维码框内点击刷新二维码</p>
