<script>
import Request from '../assets/js/base/request.js'
export default {
  name: "login",
  data() {
    return {
      yzmtu: 'http://zcda.com/code/getCode',
      autoplay: true,
      changeBtn: true,
      mobileFrom: {
        hideRequiredAsterisk: true,
        mobile: "",
        paswd: ""
      },
      mobileFromRules: {
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          {
            min: 0,
            max: 11,
            message: "您输入的手机号不正确，请重新核对输入",
            trigger: "blur"
          },
          {
            pattern: /^1[34578]\d{9}$/,
            message: "您输入的手机号不正确，请重新核对输入"
          }
        ],
        paswd: [
          {
            required: true,
            message: "您输入的密码不正确，请检查",
            trigger: "blur"
          }
        ],
        yzm: []
      },
      carousel: [
        {
          image: require("../assets/images/login/OperationalModuleBackground.png"),
          name: "朵云海",
          position: "董事长",
          company: "北京妙音网络科技有限公司",
          text:
            "职场档案提高了我们企业招聘的效率，而且是精准匹配工作岗位，真正是因才提供岗位。"
        },
        {
          image: require("../assets/images/login/Wechatwasselected.png"),
          name: "朵云海",
          position: "董事长",
          company: "北京妙音网络科技有限公司",
          text:
            "职场档案提高了我们企业招聘的效率，而且是精准匹配工作岗位，真正是因才提供岗位。"
        },
        {
          image: require("../assets/images/login/Wechatwasselected.png"),
          name: "朵云海",
          position: "董事长",
          company: "北京妙音网络科技有限公司",
          text:
            "职场档案提高了我们企业招聘的效率，而且是精准匹配工作岗位，真正是因才提供岗位。"
        }
      ]
    };
  },
  methods: {
    changeBtns: function() {
      this.changeBtn = !this.changeBtn;
    },
    changeyzm: function () {
      let yzm = this.yzmtu
      this.yzmtu = ''
      return this.yzmtu = yzm + '?d=' + Math.random() * 10
    },
    login: function () {
      console.log(Request)
      Request.login({
        data: {
          mobile: this.mobileFrom.mobile,
          password: this.mobileFrom.paswd,
          yzm:this.mobileFrom.yzm
        }
      }).then((res) => {
        console.log(res)
      }).catch((res) => {
        console.log(res)
      })
    }
  }
};
</script>

