import Vue from 'vue'
import Router from 'vue-router'

// 登录
import login from '@/views/login'

// 首页模板
import index from '@/views/index'

// 最新活跃页
import latestActive from '@/components/index/latestActive'

// 管理员工档案页
import manageStaffFiles from '@/components/index/manageStaffFiles'

// 员工奖惩列表页
import staffCommendationList from '@/components/index/staffCommendationList'
// 员工奖惩列表页->员工嘉奖详情
import staffCommendationInfo from '@/components/staffCommendationList/staffCommendationInfo'

// 管理招聘档案页
import manageRecruitmentFiles from '@/components/index/manageRecruitmentFiles'
// 管理招聘档案页->授权过我的
import authorizationed from '@/components/manageRecruitmentFiles/authorizationed'
// 管理招聘档案页->授权待通过
import authorizationing from '@/components/manageRecruitmentFiles/authorizationing'
// 管理招聘档案页->拒绝授权
import denyAuthorization from '@/components/manageRecruitmentFiles/denyAuthorization'
// 管理招聘档案页->发布职位招聘信息
import releaseJobInfo from '@/components/manageRecruitmentFiles/releaseJobInfo'
// 管理招聘档案页->看过我
import readMe from '@/components/manageRecruitmentFiles/readMe'
// 管理招聘档案页->职位详情
import jobDetails from '@/components/manageRecruitmentFiles/jobDetails'
// 管理招聘档案页->公司详情
import companyDetails from '@/components/manageRecruitmentFiles/companyDetails'
// 管理招聘档案页->热招职位
import hotTrick from '@/components/manageRecruitmentFiles/hotTrick'
// 沟通
import communicate from '@/components/communicate/index'

Vue.use(Router)

export default new Router({
  base:'/',
  mode: 'hash',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      children: [
        {
          path: '/index/latestActive',
          name: 'latestActive',
          component: latestActive
        },
        {
          path: '/index/manageStaffFiles',
          name: 'manageStaffFiles',
          component: manageStaffFiles
        },
        {
          path: '/index/staffCommendationList',
          name: 'staffCommendationList',
          component: staffCommendationList
        },
        {
          path: '/index/staffCommendationList/staffCommendationInfo',
          name: 'staffCommendationInfo',
          component: staffCommendationInfo
        },
        {
          path: '/index/manageRecruitmentFiles',
          name: 'manageRecruitmentFiles',
          component: manageRecruitmentFiles
        },
        {
          path: '/index/manageRecruitmentFiles/authorizationed',
          name: 'authorizationed',
          component: authorizationed
        },
        {
          path: '/index/manageRecruitmentFiles/authorizationing',
          name: 'authorizationing',
          component: authorizationing
        },
        {
          path: '/index/manageRecruitmentFiles/denyAuthorization',
          name: 'denyAuthorization',
          component: denyAuthorization
        },
        {
          path: '/index/manageRecruitmentFiles/releaseJobInfo',
          name: 'releaseJobInfo',
          component: releaseJobInfo
        },
        {
          path: '/index/manageRecruitmentFiles/readMe',
          name: 'readMe',
          component: readMe
        },
        {
          path: '/index/manageRecruitmentFiles/jobDetails',
          name: 'jobDetails',
          component: jobDetails
        },
        {
          path: '/index/manageRecruitmentFiles/companyDetails',
          name: 'companyDetails',
          component: companyDetails
        },
        {
          path: '/index/manageRecruitmentFiles/hotTrick',
          name: 'hotTrick',
          component: hotTrick
        },
        {
          path: '/index/communicate',
          name: 'communicate',
          component: communicate
        }
      ]
    }
  ]
})



