<template>
  <div class='index qFlex qFlexDirectionColumn'>
    <div class='indexTop'>
      <div
        class='logo qFlex qFlexJustifyCenter qFlexAlignCenter'
        :style='changeState.logoWidth[leftChangeState]'
      >
        <img :src='changeState.logoimg[leftChangeState]'>
      </div>
      <div class='tabs'>
        <div class='tabsItem' v-for='item in topBtn' :key='item.id'>{{item.text}}</div>
      </div>
      <!-- <div class='seach'>
        <input type='text' placeholder='查询您感兴趣的人'>
        <i class='el-icon-search'></i>
      </div>-->
      <div class='topRight'>
        <div class='qiyeban'>企业版</div>
        <div class='mail'>
          <img src='../assets/images/index/news.png'>
        </div>
        <div class='userimage' @click="myclick">
          <img :src='headimg'>
        </div>
      </div>
    </div>
    <div class='content qFlex qFlexJustifyStart'>
      <div class='indexLeft' :style='{width: changeState.leftBtnWidth[leftChangeState]}'>
        <div class='leftBtn'>
          <div class='leftBtnItem qFlex qFlexJustifyStart'>
            <!-- leftChangeState -->
            <div class='leftBtnIcon'>
              <div
                class='iconItem qFlex qFlexJustifyCenter qFlexAlignCenter'
                :class="{active: item.active}"
                v-for='(item, index) in leftBtn.btnItem[0]'
                :key='item.id'
                @click='leftBtnItem(index)'
              >
                <router-link :to='item.to'>
                  <img :src='item[item.active]'>
                </router-link>
              </div>
            </div>
            <div class='leftBtnText' v-show='leftChangeState == 0 ? false : true'>
              <div
                class='textItem'
                :class="{active: item.active}"
                v-for='(item, index) in leftBtn.btnItem[1]'
                :key='item.id'
                @click='leftBtnItem(index)'
              >
                <router-link :to='item.to'>
                  <span>{{item.text}}</span>
                </router-link>
              </div>
            </div>
          </div>
        </div>
        <div class='leftBtnState' @click='leftBtnChangeState'>
          <img :src='changeState.leftBtnState[leftChangeState]'>
        </div>
      </div>
      <div class='indexMain' :style="indexMainHeight">
        <div class='breadcrumb'>
          <el-breadcrumb class='qFlex qFlexAlignCenter' separator-class='el-icon-arrow-right'>
            <el-breadcrumb-item class='active' :to="{ path: '/' }">最新活跃</el-breadcrumb-item>
            <el-breadcrumb-item>个人简历</el-breadcrumb-item>
            <el-breadcrumb-item>简历详情</el-breadcrumb-item>
            <el-breadcrumb-item>编辑个人简历</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class='mainContent'>
          <router-view></router-view>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jQuery';
import Request from '../assets/js/base/request.js';
export default {
  name: 'index',
  data () {
    return {
      headimg: '',
      title: 'index',
      leftChangeState: 0,
      indexMainHeight: '',
      changeState: {
        logoimg: [
          require('../assets/images/index/FoldedLOGO.png'),
          require('../assets/images/index/LOGOafterdeployment.png')
        ],
        logoWidth: ['width: 0.921053rem', 'width: 2.894737rem'],
        leftBtnState: [
          require('../assets/images/index/Extensionbutton.png'),
          require('../assets/images/index/Shrinkbutton.png')
        ],
        leftBtnWidth: ['0.921053rem', '2.894737rem']
      },
      topBtn: [
        { text: '面试', to: '' },
        { text: 'offer', to: '' },
        { text: '档案', to: '' },
        { text: '职场利器', to: '' },
        { text: '免费权益', to: '' }
      ],
      leftBtn: {
        btnItem: [
          [
            {
              active: 1,
              0: require('../assets/images/index/active.png'),
              1: require('../assets/images/index/active2.png'),
              to: '/index/latestActive'
            },
            {
              active: 0,
              0: require('../assets/images/index/Darkhorseintheworkplace.png'),
              1: require('../assets/images/index/Darkhorseintheworkplace2.png'),
              to: ''
            },
            {
              active: 0,
              0: require('../assets/images/index/Workplaceservice.png'),
              1: require('../assets/images/index/Workplaceservice2.png'),
              to: ''
            },
            {
              active: 0,
              0: require('../assets/images/index/Managementfiles.png'),
              1: require('../assets/images/index/Managementfiles2.png'),
              to: ''
            },
            {
              active: 0,
              0: require('../assets/images/index/Employeefiles.png'),
              1: require('../assets/images/index/Employeefiles2.png'),
              to: '/index/manageStaffFiles'
            },
            {
              active: 0,
              0: require('../assets/images/index/news.png'),
              1: require('../assets/images/index/news2.png'),
              to: ''
            },
            {
              active: 0,
              0: require('../assets/images/index/Myinformation.png'),
              1: require('../assets/images/index/Myinformation2.png'),
              to: ''
            }
          ],
          [
            { text: '最新活跃', active: 1, to: '/index/latestActive'},
            { text: '职场黑马', active: 0, to: '/index/latestActive'},
            { text: '职场服务', active: 0, to: '/index/latestActive'},
            { text: '档案招聘管理', active: 0, to: '/index/latestActive'},
            { text: '管理员工档案', active: 0, to: '/index/manageStaffFiles'},
            { text: '消息', active: 0, to: '/index/latestActive'},
            { text: '我的资料', active: 0, to: '/index/latestActive'}
          ]
        ]
      }
    }
  },
  created () {
    
      Request.test({}).then((res) => {
        console.log(res)
        this.headimg = res.avatar
      }).catch((res) => {
        console.log(res)
      })
  },
  methods: {
    leftBtnChangeState: function () {
      return this.leftChangeState === 0
        ? (this.leftChangeState = 1)
        : (this.leftChangeState = 0)
    },
    leftBtnItem: function (index) {
      $('.leftBtnIcon .iconItem')
        .eq(index)
        .addClass('active')
        .siblings()
        .removeClass('active')
      $('.leftBtnText .textItem')
        .eq(index)
        .addClass('active')
        .siblings()
        .removeClass('active')
      for (var i = 0; i < this.leftBtn.btnItem[0].length; i++) {
        if (i === index) {
          this.leftBtn.btnItem[0][i].active = 1
          this.leftBtn.btnItem[1][i].active = 1
        } else {
          this.leftBtn.btnItem[0][i].active = 0
          this.leftBtn.btnItem[1][i].active = 0
        }
      }
    },
    myclick: function () {
      console.log(1)
      Request.test({}).then((res) => {
        console.log(res)
        this.headimg = res.avatar
      }).catch((res) => {
        console.log(res)
      })
    }
  },
  watch: {
  }
}
</script>

<style scoped>
@import '../assets/css/index/index.css';
</style>



