<template>
<!-- 最新活跃页 -->
  <div class='latestActive'>
    <div class='topBtn qFlex qFlexJustifyBetween'>
      <div class='topLeftBtn qFlex qFlexJustifyStart'>
        <router-link
          class='LeftBtnItem'
          :class='{active: item.active}'
          v-for='item in activeLeftBtn'
          :key='item.id'
          tag='a'
          to
        >
          <span>{{item.text}}</span>
        </router-link>
      </div>
      <div class='topRightBtn qFlex qFlexJustifyStart'>
        <router-link
          class='RightBtnItem'
          :class='{active: item.active}'
          v-for='item in activeRightBtn'
          :key='item.id'
          tag='a'
          to
        >
          <span>{{item.text}}</span>
          <i :class='[item.active ? "el-icon-caret-top" : "el-icon-caret-bottom"]'></i>
        </router-link>
      </div>
    </div>
    <div class="latestActiveContent" v-for="item in pagination.latestActiveContent" :key="item.id">
      <div class="item qFlex qFlexJustifyStart">
        <div class="headImage">
          <img :src="item.headImage"/>
        </div>
        <div class="info qFlex qFlexDirectionColumn">
          <div class="qFlex qFlexJustifyStart">
            <div class="name">{{item.name}}</div>
            <div class="salary">{{item.salary}}</div>
          </div>
          <div class="oneselfInfo qFlex qFlexJustifyStart">
            <div class="workingHours">{{item.workingHours}}</div>
            <div class="education">{{item.education}}</div>
            <div class="tags qFlex qFlexJustifyStart showByStep">
              <div v-for="item1 in item.tags" :key="item1.id">{{item1}}</div>
            </div>
          </div>
        </div>
        <div class="text">
          <div>{{item.position}}</div>
          <div class="showByStep">{{item.text}}</div>
        </div>
        <div class="lastTime" :class="[item.lastTime.type]">{{item.lastTime.text}}</div>
      </div>
    </div>
    <div class="qFlex qFlexJustifyCenter">
      <div class="pagination qFlex qFlexJustifyStart">
        <div class="firstPage">首页</div>
        <div class="prevPage">上一页</div>
        <div class="pageList">
          <div>1</div>
        </div>
        <div class="nextPage">下一页</div>
        <div class="lastPage">尾页</div>
        <div class="pages">共<span>2</span>页</div>
        <div class="sizes">共<span>2</span>条记录</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'latestActive',
  data () {
    return {
      activeLeftBtn: [
        { text: '大数据挖掘工程师', active: 1 },
        { text: 'UI/UE设计师', active: 0 },
        { text: '产品经理助理', active: 0 },
        { text: '+' }
      ],
      activeRightBtn: [
        { text: '推荐', active: 1 },
        { text: '城市', active: 0 },
        { text: '公司', active: 0 },
        { text: '要求', active: 0 }
      ],
      pagination: {
        currentPage: 1,
        pageSize: 10,
        latestActiveContent: [
          {
            headImage: require('../../assets/images/index/userImage.png'),
            name: '孙定尖',
            salary: '10K-26K',
            workingHours: '3年',
            education: '本科',
            tags: ['企业CEO', '领导能力强', '技术控'],
            position: '大数据挖掘工程师',
            text: '生活百般滋味，人生需要笑对，人活着，不要太高傲，三十年河东，三十年河西，潮起潮又落。',
            lastTime: {text: '立即沟通', type: 'newTime'}
          },
          {
            headImage: require('../../assets/images/index/userImage.png'),
            name: '孙定尖',
            salary: '10K-26K',
            workingHours: '3年',
            education: '本科',
            tags: ['企业CEO', '领导能力强', '技术控'],
            position: '大数据挖掘工程师',
            text: '生活百般滋味，人生需要笑对，人活着，不要太高傲，三十年河东，三十年河西，潮起潮又落。生活百般滋味，人生需要笑对，人活着，不要太高傲，三十年河东，三十年河西，潮起潮又落。',
            lastTime: {text: '30分钟前', type: 'oldTime'}
          }
        ]
      }
    }
  },
  methods: {
    changeLeftBtn: function() {},
    changeRightBtn: function() {},
    pagination: function () {}
  }
}
</script>

<style scoped>
@import '../../assets/css/index/latestActive.css';
</style>



