<template>
  <!-- 沟通 -->
  <div class="communicate">
    <div class="qFlex qFlexJustifyStart">
      <div class="left qFlex qFlexDirectionColumn">
        <div class="seach qFlex qFlexJustifyStart">
          <div class="cascader">
            <el-cascader :options="searchCascader" v-model="search.type"></el-cascader>
          </div>
          <div class="seachInput">
            <input type="text" v-model="search.value">
            <i class="el-icon-search"></i>
          </div>
        </div>
        <div class="chatList">
          <div class="defaultClass">
            <div class="defaultItem">
              <div
                class="chatItem qFlex qFlexJustifyStart"
                v-for="defaultItem in chatList.defaultList"
                :key="defaultItem"
                
              >
                <div class="qFlex qFlexAlignCenter">
                  <img class="check" src="../../assets/images/manageStaffFiles/Checkbox.png" alt>
                </div>
                <div class="headimg qFlex qFlexAlignCenter">
                  <img class="chatHeadimg" src="../../assets/images/index/userImage.png" alt>
                  <img class="blackhorse" src="../../assets/images/communicate/Blackhorse.png" alt>
                </div>
                <div class="chatInfo">
                  <div class="chatName showByStep">刘威</div>
                  <div class="chatText showByStep">Are you ok?Are you ok?Are you ok?</div>
                </div>
                <div class="qFlex qFlexAlignCenter">
                  <div class="news">2</div>
                </div>
              </div>
            </div>
            <div class="interestClass otherClass qFlex qFlexJustifyStart">
              <div class="chatHeadimg qFlex qFlexAlignCenter">
                <img src="../../assets/images/communicate/Interestedinme.png" alt>
              </div>
              <div class="classInfo">
                <div class="chatName showByStep">对我感兴趣的</div>
                <div class="chatText showByStep">Are you ok?Are you ok?Are you ok?</div>
              </div>
            </div>
            <div class="unsuitedClass otherClass qFlex qFlexJustifyStart">
              <div class="chatHeadimg qFlex qFlexAlignCenter">
                <img src="../../assets/images/communicate/Uninterested.png" alt>
              </div>
              <div class="classInfo">
                <div class="chatName showByStep">不合适</div>
                <div class="chatText showByStep">Are you ok?</div>
              </div>
            </div>
            <div class="collectionClass otherClass qFlex qFlexJustifyStart">
              <div class="chatHeadimg qFlex qFlexAlignCenter">
                <img src="../../assets/images/communicate/Collection.png" alt>
              </div>
              <div class="classInfo">
                <div class="chatName showByStep">我收藏的</div>
                <div class="chatText showByStep">Are you ok?</div>
              </div>
            </div>
            <div class="communicatedClass otherClass qFlex qFlexJustifyStart">
              <div class="chatHeadimg qFlex qFlexAlignCenter">
                <img src="../../assets/images/communicate/Hascommunicated.png" alt>
              </div>
              <div class="classInfo">
                <div class="chatName showByStep">已经沟通过的</div>
                <div class="chatText showByStep">Are you ok?</div>
              </div>
            </div>
          </div>
          <div class="interestItem"></div>
          <div class="unsuitedItem"></div>
          <div class="collectionItem"></div>
          <div class="communicatedItem"></div>
        </div>
      </div>
      <div class="rigth"></div>
    </div>
  </div>
</template>

<script>
import communicate from "../../assets/js/communicate/index.js";
export default {
  name: "communicate",
  data() {
    return {
      searchCascader: communicate.searchCascader,
      search: communicate.search,
      chatList: communicate.chatList
    };
  }
};
</script>

<style scoped>
@import "../../assets/css/communicate/index.css";
</style>



