<template>
  <!-- 管理招聘档案 -->
  <div class="manageRecruitmentFiles">
    <div class="topBtn qFlex qFlexJustifyBetween">
      <div class="topLeftBtn qFlex qFlexJustifyStart">
        <router-link
          class="LeftBtnItem"
          :class="{active: item.active}"
          v-for="item in activeLeftBtn"
          :key="item.id"
          tag="a"
          to
        >
          <span>{{item.text}}</span>
        </router-link>
      </div>
      <div class="topRightBtn qFlex qFlexJustifyStart">
        <div>发布职位</div>
      </div>
    </div>
    <div class="tables">
      <table cellspacing="0" cellpadding="0">
        <thead>
          <tr>
            <td class="allCheckBtn">
              <img
                :src="allElectionBtn.icon[allElectionBtn.active]"
                alt="全选按钮"
                @click="allElection"
              >
            </td>
            <td class="jobInfoBtn">
              <span>招聘岗位信息</span>
              <font class="shuxian"></font>
            </td>
            <td class="releaseStateBtn">
              <span>发布状态</span>
              <i
                :class="[releaseStateBtn.icon[releaseStateBtn.active]]"
                :style="releaseStateBtn.color[releaseStateBtn.active]"
              ></i>
              <font class="shuxian"></font>
            </td>
            <td class="matchingDegreeBtn">
              <span>岗位匹配度人选</span>
              <font class="shuxian"></font>
            </td>
            <td class="releaseTimeBtn">
              <span>发布时间</span>
              <font class="shuxian"></font>
            </td>
            <td class="operationBtn">
              <span>操作</span>
            </td>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in tbody"
            :key="index"
            :class="{active: item.active}"
            @mousemove="tabMouseleave(index)"
            @mouseout="tabMouseout"
          >
            <td class="checkBtn">
              <img :src="item.checkBtn.icon[item.checkBtn.active]" @click="singleElection(index)">
            </td>
            <td class="jobInfo">
              <div class="name">{{item.jobInfo.name}}</div>
              <div class="tags qFlex qFlexJustifyStart">
                <div>
                  <span>{{item.jobInfo.tags.address}}</span>
                </div>
                <font class="shuxian"></font>
                <div>
                  <span>{{item.jobInfo.tags.time}}</span>
                </div>
                <font class="shuxian"></font>
                <div>
                  <span>{{item.jobInfo.tags.education}}</span>
                </div>
                <font class="shuxian"></font>
                <div>
                  <span>{{item.jobInfo.tags.salary}}</span>
                </div>
              </div>
            </td>
            <td class="releaseState">
              <div
                :style="item.releaseState.color[item.releaseState.active]"
              >{{item.releaseState.state[item.releaseState.active]}}</div>
            </td>
            <td class="matchingDegree">
              <div class="qFlex qFlexJustifyAround">
                <div>
                  <span :class="[item.releaseState.active===2? 'active': '']">
                    <font class="num">{{item.matchingDegree.see}}</font>人查看
                  </span>
                </div>
                <font class="shuxian"></font>
                <div>
                  <span>
                    <font class="num">{{item.matchingDegree.communicate}}</font>人沟通
                  </span>
                </div>
                <font class="shuxian"></font>
                <div>
                  <span>
                    <font class="num">{{item.matchingDegree.authorization}}</font>人通过授权
                  </span>
                </div>
              </div>
            </td>
            <td class="releaseTime">
              <span>{{item.releaseTime}}发布</span>
            </td>
            <td class="operation">
              <div>编辑</div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="qFlex qFlexJustifyCenter">
      <div class="deleteBtn" @click="deleteItem">
        <img :src="deleteBtn.icon[deleteBtn.a]">
        <span :style="deleteBtn.color[deleteBtn.a]">删除</span>
      </div>
      <div class="pagination qFlex qFlexJustifyStart">
        <div class="firstPage">首页</div>
        <div class="prevPage">上一页</div>
        <div class="pageList">
          <div>1</div>
        </div>
        <div class="nextPage">下一页</div>
        <div class="lastPage">尾页</div>
        <div class="pages">
          共
          <span>2</span>页
        </div>
        <div class="sizes">
          共
          <span>2</span>条记录
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "manageRecruitmentFiles",
  data() {
    return {
      activeLeftBtn: [
        { text: "大数据挖掘工程师", active: 1 },
        { text: "UI/UE设计师", active: 0 },
        { text: "产品经理助理", active: 0 },
        { text: "+" }
      ],
      allElectionBtn: {
        active: 0,
        icon: [
          require("../../assets/images/manageStaffFiles/Checkboxunchecked.png"),
          require("../../assets/images/manageStaffFiles/Checkbox.png")
        ]
      },
      deleteBtn: {
        a: 0,
        icon: [
          require("../../assets/images/manageStaffFiles/Deleteinactivated.png"),
          require("../../assets/images/manageStaffFiles/Deleteactivation.png")
        ],
        color: ["color: rgba(238, 238, 238, 1);", "color: rgba(253,37,37,1);"]
      },
      releaseStateBtn: {
        active: 0,
        icon: ["el-icon-caret-bottom", "el-icon-caret-top"],
        color: ["color: rgba(51, 51, 51, 1)", "color: rgba(63, 178, 232, 1)"]
      },
      tbody: [
        {
          active: 0,
          checkBtn: {
            active: 0,
            icon: [
              require("../../assets/images/manageStaffFiles/Checkboxunchecked.png"),
              require("../../assets/images/manageStaffFiles/Checkbox.png")
            ]
          },
          jobInfo: {
            name: "UI/交互设计师",
            tags: {
              address: "北京",
              time: "1-3年",
              education: "本科",
              salary: "10K"
            }
          },
          releaseState: {
            active: 0,
            state: ["", "已关闭", "审核失败"],
            color: [
              "",
              "background: rgba(210,210,210,1)",
              "background:rgba(250,81,104,1);"
            ]
          },
          //查看/同意/授权
          matchingDegree: {
            see: "59",
            communicate: "28",
            authorization: "12"
          },
          releaseTime: "2018-11-11"
        },
        {
          active: 0,
          checkBtn: {
            active: 0,
            icon: [
              require("../../assets/images/manageStaffFiles/Checkboxunchecked.png"),
              require("../../assets/images/manageStaffFiles/Checkbox.png")
            ]
          },
          jobInfo: {
            name: "UI/交互设计师",
            tags: {
              address: "北京",
              time: "1-3年",
              education: "本科",
              salary: "10K"
            }
          },
          releaseState: {
            active: 1,
            state: ["", "已关闭", "审核失败"],
            color: [
              "",
              "background: rgba(210,210,210,1)",
              "background:rgba(250,81,104,1);"
            ]
          },
          //查看/同意/授权
          matchingDegree: {
            see: "59",
            communicate: "28",
            authorization: "12"
          },
          releaseTime: "2018-11-11"
        },
        {
          active: 0,
          checkBtn: {
            active: 0,
            icon: [
              require("../../assets/images/manageStaffFiles/Checkboxunchecked.png"),
              require("../../assets/images/manageStaffFiles/Checkbox.png")
            ]
          },
          jobInfo: {
            name: "UI/交互设计师",
            tags: {
              address: "北京",
              time: "1-3年",
              education: "本科",
              salary: "10K"
            }
          },
          releaseState: {
            active: 2,
            state: ["", "已关闭", "审核失败"],
            color: [
              "",
              "background: rgba(210,210,210,1)",
              "background:rgba(250,81,104,1);"
            ]
          },
          //查看/同意/授权
          matchingDegree: {
            see: "59",
            communicate: "28",
            authorization: "12"
          },
          releaseTime: "2018-11-11"
        }
      ]
    };
  },
  methods: {
    /**
     * 鼠标滑过
     */
    tabMouseleave: function(index) {
      for (var i = 0; i < this.tbody.length; i++) {
        if (index === i) {
          this.tbody[i].active = 1;
        } else {
          this.tbody[i].active = 0;
        }
      }
    },
    tabMouseout: function() {
      for (var i = 0; i < this.tbody.length; i++) {
        this.tbody[i].active = 0;
      }
    },
    /**
     * 单选 checkBtn.icon[checkBtn.active]
     */
    singleElection: function(index) {
      var all = 0;
      for (var i = 0; i < this.tbody.length; i++) {
        if (index === i) {
          if (this.tbody[i].checkBtn.active) {
            this.tbody[i].checkBtn.active = 0;
          } else {
            this.tbody[i].checkBtn.active = 1;
          }
        }
        if (this.tbody[i].checkBtn.active) {
          all++;
        }
      }
      /**
       * 勾选全选按钮
       */
      if (all === this.tbody.length) {
        this.allElectionBtn.active = 1;
      } else {
        this.allElectionBtn.active = 0;
      }
      /**
       * 显示删除
       */
      if (all > 0) {
        this.deleteBtn.a = 1;
      } else {
        this.deleteBtn.a = 0;
      }
    },
    /**
     * 全选
     */
    allElection: function() {
      if (this.allElectionBtn.active) {
        this.allElectionBtn.active = 0;
      } else {
        this.allElectionBtn.active = 1;
      }
      for (var i = 0; i < this.tbody.length; i++) {
        this.tbody[i].checkBtn.active = this.allElectionBtn.active;
      }
      this.allElectionBtn.active = this.allElectionBtn.active;
      this.deleteBtn.a = this.allElectionBtn.active;
    },
    /**
     * 删除
     */
    deleteItem: function() {
      for (var i = 0; i < this.tbody.length; i++) {
        if (this.tbody[i].checkBtn.active === 1) {
          this.tbody.splice(1, i);
        }
      }
    }
  }
};
</script>

<style scoped>
@import "../../assets/css/index/manageRecruitmentFiles.css";
</style>



