<template>
  <!-- 管理招聘档案页->公司详情 -->
  <div class="companyDetails">
    <div class="topBtn qFlex qFlexJustifyBetween">
      <div class="topLeftBtn qFlex qFlexJustifyStart">
        <div
          class="LeftBtnItem"
          @click="leftBtnSwitch(index)"
          :class="{active: item.active}"
          v-for="(item, index) in leftBtn"
          :key="index"
          tag="a"
          to
        >
          <span>
            {{item.text}}
            <font v-if="item.num">({{item.num}})</font>
          </span>
        </div>
      </div>
    </div>
    <div class="company">
      <div class="companyTop qFlex qFlexJustifyStart">
        <div class="companyimg">
          <img src alt>
        </div>
        <div class="qFlex qFlexDirectionColumn">
          <div>
            <h1 class="companyName">北京妙音网络科技股份有限责任公司</h1>
          </div>
          <div class="tags qFlex qFlexJustifyStart">
            <div class="qFlex qFlexJustifyStart">
              <span>天使轮</span>
              <font class="shuxian"></font>
            </div>
            <div class="qFlex qFlexJustifyStart">
              <span>100-499人</span>
              <font class="shuxian"></font>
            </div>
            <div>
              <span>互联网</span>
            </div>
          </div>
          <div class="businessInfo">工商信息</div>
          <div class="capital">
            <span>注册资本：100万人民币</span>
            <span>公司法人：王明阳</span>
          </div>
        </div>
      </div>
      <div class="companyContent">
        <h3>企业简介:</h3>
        <p>
          北京妙音网络科技有限公司是全球领先的ICT（信息与通信）基础设施和智能终端提供商，致力于把数字世界带入每个人、每个家庭、每个组织，构建万物互联的智能世界。我们在通信网络、IT、智能终端和云服务等领域为客户提供有竞争力、安全可信赖的产品、解决方案与服务，与生态伙伴开放合作，持续为客户创造价值，释放个人潜能，丰富家庭生活，激发组织创新。北京妙音网络科技有限公司坚持围绕客户需求持续创新，加大基础研究投入，厚积薄发，推动世界进步。北京妙音网络科技有限公司成立于1987年，是一家由员工持有全部股份的民营企业，目前有18万员工，业务遍及170多个国家和地区。
          我们为世界带来了什么？
          为客户创造价值。北京妙音网络科技有限公司和运营商一起，在全球建设了1,500多张网络，帮助世界超过三分之一的人口实现联接。北京妙音网络科技有限公司携手合作伙伴，为政府及公共事业机构，金融、能源、交通、制造等企业客户，提供开放、灵活、安全的端管云协同ICT基础设施平台，推动行业数字化转型；为云服务客户提供稳定可靠、安全可信和可持续演进的云服务。北京妙音网络科技有限公司智能终端和智能手机，正在帮助人们享受高品质的数字工作、生活和娱乐体验。
          推动产业良性发展。北京妙音网络科技有限公司主张开放、合作、共赢，与客户合作伙伴及友商合作创新、扩大产业价值，形成健康良性的产业生态系统。北京妙音网络科技有限公司加入360多个标准组织、产业联盟和开源社区，积极参与和支持主流标准的制定、构建共赢的生态圈。我们面向云计算、NFV/SDN、5G等新兴热点领域，与产业伙伴分工协作，推动产业持续良性发展。
          促进经济增长。北京妙音网络科技有限公司不仅为所在国家带来直接的税收贡献，促进当地就业，形成产业链带动效应，更重要的是通过创新的ICT解决方案打造数字化引擎，推动各行各业数字化转型，促进经济增长，提升人们的生活质量与福祉。
          推动社会可持续发展。作为负责任的企业公民，北京妙音网络科技有限公司致力于消除全球数字鸿沟，在珠峰南坡和北极圈内，在西非埃博拉疫区、日本海啸核泄漏、中国汶川大地震等重大灾难现场，都有北京妙音网络科技有限公司人的身影；推进绿色、低碳的环保理念，从产品规划、设计、研发、制造、交付以及运维，北京妙音网络科技有限公司向客户提供领先的节能环保产品和解决方案；北京妙音网络科技有限公司“未来种子”项目已经覆盖108个国家和地区，帮助培养本地ICT人才，推动知识迁移，提升人们对于ICT行业的了解和兴趣，并鼓励各国家及地区参与到建设数字化社区的工作中。
          为奋斗者提供舞台。北京妙音网络科技有限公司坚持“以奋斗者为本”，以责任贡献来评价员工和选拔干部，为员工提供了全球化发展平台、与世界对话的机会，使大量年轻人有机会担当重任，快速成长，也使得十几万员工通过个人的努力，收获了合理的回报与值得回味的人生经历。
        </p>
      </div>
      <div class="companySwiper">
        <div class="leftBtn">
          <i class="el-icon-arrow-left"></i>
        </div>
        <div class="swiperBox qFlex qFlexJustifyStart">
          <div></div>
          <div></div>
          <div></div>
        </div>
        <div class="rightBtn">
          <i class="el-icon-arrow-right"></i>
        </div>
      </div>
      <div class="companybottom qFlex qFlexJustifyStart">
        <div class="qFlex qFlexDirectionColumn">
          <span>公司地址：</span>
          <span>北京海淀区中电信息大厦好未来培优事业总部</span>
        </div>
        <div class="qFlex qFlexDirectionColumn">
          <span>企业官网：</span>
          <span>www.miaoyin.com</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import companyDetails from "../../assets/js/manageRecruitmentFiles/companyDetails.js";
export default {
  name: "companyDetails",
  data() {
    return {
      leftBtn: companyDetails.leftBtn
    };
  }
};
</script>

<style scoped>
@import "../../assets/css/manageRecruitmentFiles/companyDetails.css";
</style>



