<template>
  <el-rate class="rate" v-model="value" :colors="['#FF7A22','#FF7A22','#FF7A22']" void-color="#E2E2E2"></el-rate>
</template>

<script>
export default {
  name: "rate",
  props: ["value"]
};
</script>

<style scoped>
.rate {
  margin-top: -11px;
  height: 10px;
}
</style>



