<template>
  <!-- 员工嘉奖详情 -->
  <div class="staffCommendationInfo">
    <h1>员工嘉奖信息</h1>
    <div class="infoBox">
      <div class="info qFlex qFlexJustifyStart">
        <div>
          <img class="staffImage" src="../../assets/images/index/userImage.png">
        </div>
        <div class="qFlex qFlexDirectionColumn">
          <div class="staffname">
            <span>岳冰冰</span>
          </div>
          <div class="staffInfo qFlex qFlexDirectionColumn">
            <div class="qFlex qFlexJustifyStart">
              <div class="bianju">
                <span>所属部门:</span>
                <span>产品研发部</span>
              </div>
              <div class="bianju">
                <span>职位:</span>
                <span>产品经理</span>
              </div>
            </div>
            <div class="bianju">
              <span>入职时间:</span>
              <span>2018-11-11</span>
            </div>
          </div>
        </div>
      </div>
      <div class="operation qFlex qFlexJustifyStart">
        <div
          @click="checkOperation(index)"
          :class="{active: item.active}"
          v-for="(item, index) in operation.operationList"
          :key="index"
        >
          <img :src="item.icon[item.active]" :alt="item.text" :class="['operation'+index]">
          <span :style="item.color[item.active]">{{item.text}}</span>
        </div>
      </div>
      <div class="operationBox">
        <div class="operationBox0"></div>
        <div class="operationBox1"></div>
        <div class="operationBox2">
          <div class="title">填写奖惩</div>
          <div>
            <table cellspacing="0" cellpadding="0">
              <thead>
                <tr>
                  <td class="timeBtn">
                    <span>奖惩时间</span>
                    <font class="shuxian"></font>
                  </td>
                  <td class="typeBtn">
                    <span>奖惩类型</span>
                    <font class="shuxian"></font>
                  </td>
                  <td class="textBtn">
                    <span>描述</span>
                    <font class="shuxian"></font>
                  </td>
                  <td class="evaluateBtn">
                    <span>技能评价</span>
                  </td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="items in operationBox2.tbody" :key="items.id">
                  <td v-for="(item, index) in items" :key="index" v-if="!item.rate">
                    <span class="showByStep">{{item.content}}</span>
                    <font class="shuxian" v-if="item.shuxian"></font>
                  </td>
                  <td v-for="(item, index) in items" :key="index" v-if="item.rate">
                    <rate value="item.content"></rate>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="operationBox3"></div>
        <div class="operationBox4"></div>
      </div>
      <div></div>
    </div>
  </div>
</template>

<script>
import rate from "../utils/rate";
export default {
  name: "staffCommendationInfo",
  components: {
    rate
  },
  data() {
    return {
      operation: {
        operationList: [
          {
            text: "与TA沟通",
            active: 0,
            icon: [
              require("../../assets/images/staffCommendationInfo/Communicate.png"),
              require("../../assets/images/staffCommendationInfo/Communicate2.png")
            ],
            color: ["color:rgba(153,153,153,1);", "color:rgba(63,178,232,1);"]
          },
          {
            text: "档案简历",
            active: 0,
            icon: [
              require("../../assets/images/staffCommendationInfo/Archivesresume.png"),
              require("../../assets/images/staffCommendationInfo/Archivesresume2.png")
            ],
            color: ["color:rgba(153,153,153,1);", "color:rgba(63,178,232,1);"]
          },
          {
            text: "奖惩日志",
            active: 1,
            icon: [
              require("../../assets/images/staffCommendationInfo/Rewardandpunishmentlog.png"),
              require("../../assets/images/staffCommendationInfo/Rewardandpunishmentlog2.png")
            ],
            color: ["color:rgba(153,153,153,1);", "color:rgba(63,178,232,1);"]
          },
          {
            text: "入职信息",
            active: 0,
            icon: [
              require("../../assets/images/staffCommendationInfo/Entryinformation.png"),
              require("../../assets/images/staffCommendationInfo/Entryinformation2.png")
            ],
            color: ["color:rgba(153,153,153,1);", "color:rgba(63,178,232,1);"]
          },
          {
            text: "办理离职",
            active: 0,
            icon: [
              require("../../assets/images/staffCommendationInfo/Handlingofresignation.png"),
              require("../../assets/images/staffCommendationInfo/Handlingofresignation2.png")
            ],
            color: ["color:rgba(153,153,153,1);", "color:rgba(63,178,232,1);"]
          }
        ]
      },
      operationBox2: {
        tbody: [
          [
            { content: "2018-11-11", shuxian: 1, rate: 0 },
            { content: "创新发明奖", shuxian: 1, rate: 0 },
            {
              content: "在本职工作岗位中，对目前的生产工具",
              shuxian: 1,
              rate: 0
            },
            { content: 4, shuxian: 0, rate: 1 }
          ],
          [
            { content: "2018-11-11", shuxian: 1, rate: 0 },
            { content: "创新发明奖", shuxian: 1, rate: 0 },
            {
              content: "在本职工作岗位中，对目前的生产工具",
              shuxian: 1,
              rate: 0
            },
            { content: 2, shuxian: 0, rate: 1 }
          ]
        ]
      }
    };
  },
  methods: {
    checkOperation: function(index) {
      for (var i = 0; i < this.operation.operationList.length; i++) {
        if (i === index) {
          this.operation.operationList[i].active = 1;
        } else {
          this.operation.operationList[i].active = 0;
        }
      }
    }
  }
};
</script>

<style scoped>
@import "../../assets/css/staffCommendationList/staffCommendationInfo.css";
</style>



