import config from '../base/config.js'
var hotTrick = {
  leftBtn: [{
    text: '公司信息',
    active: 1
  }, {
    text: '热招职位',
    num: '100',
    active: 0
  }],
  rightBtn: [{
    text: '推荐',
    active: 1
  }, {
    text: '城市',
    active: 0
  }, {
    text: '公司',
    active: 0
  }, {
    text: '要求',
    active: 0
  }],
  tbody: [{
    active: 0,
    headimg: require('../../images/index/userImage.png'),
    sex: config.sex
  }]
}

export default hotTrick



